@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:600|Noto+Sans|Open+Sans:400,700&display=swap);
:root {
  --primary: #efb6b2;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* base styles & title */
body {
  font-family: "Noto Serif";
  color: #4e4e4e;
  color: var(--secondary);
}
.App {
  /* max-width: 960px; */
  margin: 0 auto;
}
.title h1 {
  color: #efb6b2;
  color: var(--primary);
  font-size: 1.2rem;
  letter-spacing: 2px;
  font-weight: normal;
}
.title h2,
.title p {
  text-align: center;
}
.title h2 {
  margin-top: 60px;
  font-size: 2.6rem;
}

.login-upload {
  text-align: center;
  border: 2px solid #1da1f2;
  background-color: #fff;
  display: block;
  margin: 10px auto;
  padding: 1.1rem;
}

.btn {
  font-size: 100%;
  background-color: #fff;
  border: 1px solid #4e4e4e;
  border: 1px solid var(--secondary);
  padding: 0;
  border-radius: 0;
  cursor: pointer;
  padding: 0.5rem 1rem;
}
.login-btn:hover {
  background-color: #4e4e4e;
  background-color: var(--secondary);
  color: #fff;
}

/* Navbar */
.nav-bar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 100vw;
  height: 2.8rem;
  padding: 0 2rem;
  border-bottom: 1px solid #4e4e4e;
  border-bottom: 1px solid var(--secondary);
  border-radius: 0;
  font-family: "Pacifico", cursive;
  color: #efb6b2;
  color: var(--primary);
}

.nav-link {
  color: #fff;
  transition: background-color 0.5s ease;
  background-color: #4e4e4e;
  background-color: var(--secondary);
}

.nav-link:hover {
  background-color: #efb6b2;
  background-color: var(--primary);
  border: 1px solid #efb6b2;
  border: 1px solid var(--primary);
}

@media (max-width: 450px) {
  .nav-bar {
    padding: 0 0.5rem;
  }
}

/* upload form styles */
.upload-form {
  margin: 30px auto 10px;
  text-align: center;
}
.upload-label input {
  height: 0;
  width: 0;
  opacity: 0;
}
.upload-label {
  display: block;
  width: 30px;
  height: 30px;
  border: 1px solid #efb6b2;
  border: 1px solid var(--primary);
  border-radius: 50%;
  margin: 10px auto;
  line-height: 30px;
  color: #efb6b2;
  color: var(--primary);
  font-weight: bold;
  font-size: 24px;
}
.upload-label:hover {
  background: #efb6b2;
  background: var(--primary);
  color: white;
}
.output {
  height: 60px;
  font-size: 0.8rem;
}
.error {
  color: #ff4a4a;
  color: var(--error);
}

/* progress bar styles */
.progress-bar {
  height: 5px;
  background: #efb6b2;
  background: var(--primary);
  margin-top: 20px;
}

/* image grid styles */
.img-grid {
  margin: 20px 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
}
@media (max-width: 730px) {
  .img-grid {
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
  }
}
.img-wrap {
  overflow: hidden;
  height: 0;
  padding: 50% 0;
  /* padding controls height, will always be perfectly square regardless of width */
  position: relative;
  opacity: 0.8;
}
.img-wrap img {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

/* modal styles */
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}
.backdrop img {
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  border: 3px solid white;
}

* {
  margin: 0;
  padding: 0;
  border-radius: 5px;
  box-sizing: border-box;
}
.backdrop {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  font-family: sans-serif;
  -webkit-justify-content: center;
          justify-content: center;
  background-size: cover;
  background-position: center;
  background: rgba(0, 0, 0, 0.5);
}
.container {
  position: relative;
  width: 25rem;
  background: white;
  padding: 4rem 2rem;
}
/* header {
  font-size: 40px;
  margin-bottom: 60px;
  font-family: "Montserrat", sans-serif;
} */
.input-field,
form .button {
  margin: 2rem 0;
  position: relative;
  height: 3rem;
  width: 100%;
}
.input-field input {
  height: 100%;
  width: 100%;
  border: 1px solid silver;
  padding-left: 15px;
  outline: none;
  font-size: 1.2rem;
  transition: 0.4s;
}
input:focus {
  border: 1px solid #1da1f2;
}
.input-field label,
span.show {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.input-field label {
  left: 15px;
  pointer-events: none;
  color: grey;
  font-size: 18px;
  transition: 0.4s;
}
span.show {
  right: 20px;
  color: #111;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  visibility: hidden;
  font-family: "Open Sans", sans-serif;
}
input:valid ~ span.show {
  visibility: visible;
}
input:focus ~ label,
input:valid ~ label {
  -webkit-transform: translateY(-33px);
          transform: translateY(-33px);
  background: white;
  font-size: 16px;
  color: #1da1f2;
}
form .button {
  margin-top: 30px;
  overflow: hidden;
  z-index: 111;
}
.button .inner {
  position: absolute;
  height: 100%;
  width: 300%;
  left: -100%;
  z-index: -1;
  transition: all 0.4s;
  background: -webkit-linear-gradient(
    right,
    #00dbde,
    #fc00ff,
    #00dbde,
    #fc00ff
  );
}
.button:hover .inner {
  left: 0;
}
.button button {
  width: 100%;
  height: 100%;
  border: none;
  background: none;
  outline: none;
  color: white;
  font-size: 20px;
  cursor: pointer;
  font-family: "Montserrat", sans-serif;
}
.container .auth {
  margin: 35px 0 20px 0;
  font-size: 19px;
  color: grey;
}
.links {
  display: -webkit-flex;
  display: flex;
  cursor: pointer;
}
.facebook,
.google {
  height: 40px;
  width: 100%;
  border: 1px solid silver;
  border-radius: 3px;
  margin: 0 10px;
  transition: 0.4s;
}
.facebook:hover {
  border: 1px solid #4267b2;
}
.google:hover {
  border: 1px solid #dd4b39;
}
.facebook i,
.facebook span {
  color: #4267b2;
}
.google i,
.google span {
  color: #dd4b39;
}
.links i {
  font-size: 23px;
  line-height: 40px;
  margin-left: -90px;
}
.links span {
  position: absolute;
  font-size: 17px;
  font-weight: bold;
  padding-left: 8px;
  font-family: "Open Sans", sans-serif;
}
.signup {
  margin-top: 50px;
  font-family: "Noto Sans", sans-serif;
}
.signup a {
  color: #3498db;
  text-decoration: none;
}
.signup a:hover {
  text-decoration: underline;
}

